<template>
  <view class="carousel-section" :style="{'padding':px(config.pT)+' '+px(config.pL),'background': config.background}">
    <view class="pos-re" :style="{height:imgheights[swiperCurrent]+'rpx'}">
      <swiper class="carousel" :style="{height:imgheights[swiperCurrent]+'rpx'}" circular @change="swiperChange" :autoplay="true" >
        <swiper-item v-for="(item, index) in options" :key="index" class="carousel-item"  @tap="to(item)">
          <image :data-index='index' @load="imageLoad" :style="{'borderRadius':px(config.r),height:'100%'}" :src="item.url"/>
        </swiper-item>
      </swiper>
      <!-- 自定义swiper指示器 -->
      <view class="swiper-dots" v-if="options.length>0" :style="{right: config.pL + 8 + 'px',bottom: config.pT + 8 + 'px'}">
        <text class="num">{{ swiperCurrent + 1 }}</text>
        <text class="sign">/</text>
        <text class="num">{{ options.length }}</text>
      </view>
    </view>
  </view>
</template>

<script>
  export default {
    props: {
      gm_Id: [String, Number],
      options:{
        type:Array,
        default:[]
      },
      config: Object,
    },
    data() {
      return {
        swiperCurrent:0,
        imgheights:[],
        index22:0
      };
    },
    methods: {
      px(num){
        return uni.upx2px(num*2) + 'px'
      },
      to(item){
        if(!item.value){
          return
        }
        this.customTo(item, this.gm_Id)
      },
      swiperChange(e) {
        const index = e.detail.current;
        this.swiperCurrent = index;
        // this.titleNViewBackground = this.carouselList[index].background;
	  },
	  imageLoad: function (e) {
        this.index22++
        //获取图片真实宽度
        var imgwidth = e.detail.width,
		  imgheight = e.detail.height,
          //宽高比
		  ratio = imgwidth / imgheight;
		  console.log('图片',e);
        //计算的高度值
        var viewHeight = 750 / ratio;
        var imgheight = viewHeight
        var imgheights = this.imgheights
        //把每一张图片的高度记录到数组里
        imgheights[e.target.dataset['index']] = imgheight;// 改了这里 赋值给当前 index
        this.imgheights = imgheights
      },
    },
    mounted() {
    },
  };
</script>

